@import url("https://fonts.googleapis.com/css2?family=Anton&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Red+Hat+Display:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Stint+Ultra+Expanded&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
html {
  scroll-behavior: smooth;
}

body {
  font-family: "Red Hat Display", sans-serif;
  background-color: #151515;
  height: 200vh;
  margin: 0;
}

.three-tag {
  width: auto;
  margin-bottom: 25px;
}

.navbar {
  width: 100%;
}

.name-title {
  position: fixed;
  top: 60vh;
  margin: 0;
  z-index: -1;
  width: 100%;
  text-align: center;
  color: #e8e5e0;
  background-color: #151515;
  font-size: 23rem;
  font-family: "Anton", sans-serif;
  transform: scaleY(2);
}

.banner-about {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
  margin-top: -50px;
}
.banner-about p {
  width: 30%;
  font-weight: 400;
  font-size: 1.2rem;
  margin-top: 50px;
}
.banner-about .banner-buttons {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  width: 30%;
  margin-top: 50px;
}
.banner-about .banner-buttons a {
  text-decoration: none;
  color: #e8e5e0;
  border: 2px solid #e8e5e0;
  border-radius: 30px;
  background-color: #151515;
  padding: 15px 25px;
  font-weight: 600;
  font-size: 1.2rem;
}
.banner-about .banner-buttons button {
  text-decoration: none;
  outline: none;
  color: #151515;
  border: 2px solid #e8e5e0;
  border-radius: 30px;
  background-color: #e8e5e0;
  padding: 15px 25px;
  font-weight: 600;
  font-size: 1.2rem;
  cursor: pointer;
}

.content-container {
  display: flex;
  flex-direction: column;
  background-color: #e8e5e0;
  color: #151515;
  margin-top: 90vh;
  border-top-left-radius: 50px;
  border-top-right-radius: 50px;
  filter: drop-shadow(0px 1px 100px #151515);
  padding: 0;
  padding-top: 250px;
}

.section-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: nowrap;
  margin-bottom: 300px;
  padding-left: 100px;
}

.section-left {
  width: 15%;
}
.section-left h2 {
  font-size: 1.6rem;
  font-weight: 400;
}

.section-right {
  width: 85%;
}

.section-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: auto;
  padding-right: 50px;
}
.section-title .section-subtitle {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 20%;
  font-size: 0.8rem;
  font-weight: 100;
  padding: 25px;
}
.section-title .section-subtitle .mouse-drag {
  width: 30px;
  height: auto;
  transition: drag;
  animation: drag 1s;
  animation-delay: 0s;
  animation-iteration-count: infinite;
}
@keyframes drag {
  0% {
    margin-left: 0;
  }
  50% {
    margin-left: 10px;
  }
  100% {
    margin-left: 0;
  }
}
.section-title h1 {
  margin-top: 0;
  font-size: 7rem;
  width: 65%;
}
.section-title h1 span {
  color: #00946B;
}

.modal {
  position: fixed;
  top: 0;
  right: -100vw;
  width: auto;
  height: 100vh;
  background-color: #e8e5e0;
  transition: all 0.4s;
  padding: 0 15px;
  border-left: 1px solid #151515;
  z-index: 10;
  display: flex;
  /* Hide scrollbar for Chrome, Safari and Opera */
  /* Hide scrollbar for IE, Edge and Firefox */
}
.modal.experience {
  width: 50vw;
  min-height: 100vh;
  padding: 0 25px;
}
.modal.experience .experience-content-wrapper {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
}
.modal.experience .experience-content-wrapper .experience-company-info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
  margin-bottom: 25px;
}
.modal.experience .experience-content-wrapper .experience-company-info .experience-company-title {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  justify-self: flex-start;
}
.modal.experience .experience-content-wrapper .experience-company-info .experience-company-title h1 {
  font-size: 2.5rem;
}
.modal.experience .experience-content-wrapper .experience-company-info .experience-company-title p {
  font-weight: 400;
  font-size: 1.4rem;
}
.modal.experience .experience-content-wrapper .experience-company-info .experience-view-company {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 100%;
}
.modal.experience .experience-content-wrapper .experience-company-info .experience-view-company a {
  text-decoration: none;
  color: #e8e5e0;
  border: 2px solid #e8e5e0;
  border-radius: 30px;
  background-color: #151515;
  padding: 15px 25px;
  font-weight: 600;
  font-size: 1.4rem;
  text-align: center;
}
.modal.experience .experience-content-wrapper li {
  margin-bottom: 25px;
}
.modal .experience-content-wrapper::-webkit-scrollbar {
  display: none;
}
.modal .experience-content-wrapper {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
.modal.active {
  right: 0vw;
}
.modal .close-btn {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #e8e5e0;
  top: 25px;
  left: -70px;
  width: 50px;
  height: 50px;
  z-index: 10;
  padding: 10px;
  font-size: 1.8rem;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
}
.modal .certificate-image {
  align-self: center;
  margin: 0 auto;
  width: auto;
  height: 85%;
  object-fit: contain;
}

.blur {
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  width: 100vw;
  height: 100vh;
  z-index: -9;
  opacity: 0;
  transition: all 0.6s ease-in-out;
}
.blur.active {
  z-index: 9;
  opacity: 1;
}

.light-blur {
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  width: 100vw;
  height: 100vh;
  z-index: -9;
  opacity: 0;
  transition: all 0.6s ease-in-out;
}
.light-blur.active {
  z-index: 6;
  opacity: 1;
}

.contact-me-btn {
  position: fixed;
  bottom: 3%;
  right: 2%;
  text-decoration: none;
  outline: none;
  color: #00946B;
  border-radius: 30px;
  background-color: rgba(255, 255, 255, 0.5);
  padding: 15px 25px;
  font-weight: 600;
  font-size: 1.1rem;
  cursor: pointer;
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  display: none;
  opacity: 0;
  transition: all 0.4s ease-in-out;
  text-transform: uppercase;
  outline: none;
  border: 2px solid #00946B;
}
.contact-me-btn.active {
  display: block;
  opacity: 1;
}

.contact-form {
  position: fixed;
  bottom: -200vh;
  left: 50%;
  transform: translateX(-50%);
  background-color: #151515;
  border-radius: 30px;
  padding: 50px;
  color: #e8e5e0;
  transition: all 0.6s ease-in-out;
}
.contact-form form {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  justify-content: baseline;
  font-size: 1.4rem;
}
.contact-form form h1 {
  justify-self: center;
  text-align: center;
  align-self: center;
}
.contact-form form label {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-bottom: 25px;
}
.contact-form form label input {
  background: none;
  color: #e8e5e0;
  border: none;
  border-bottom: 2px solid #e8e5e0;
  outline: none;
  font-size: 1.4rem;
  padding: 10px;
  margin-left: 15px;
}
.contact-form form label.message-label {
  align-items: flex-start;
  justify-content: flex-start;
}
.contact-form form label textarea {
  background: none;
  color: #e8e5e0;
  border: none;
  border-bottom: 2px solid #e8e5e0;
  outline: none;
  font-size: 1.4rem;
  margin-left: 15px;
  align-self: flex-end;
  min-height: 100px;
}
.contact-form form button {
  text-decoration: none;
  outline: none;
  color: #151515;
  border: 2px solid #e8e5e0;
  border-radius: 30px;
  background-color: #e8e5e0;
  padding: 15px 25px;
  font-weight: 600;
  font-size: 1.4rem;
  cursor: pointer;
  align-self: center;
}
.contact-form.active {
  z-index: 7;
  bottom: 10%;
}
.contact-form .contact-form-close-btn {
  position: absolute;
  top: 0;
  right: 5%;
  cursor: pointer;
}

footer h1 {
  margin-top: 0;
  font-size: 7rem;
  width: 55%;
  text-align: center;
}
footer h1 span {
  color: #00946B;
}

